import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
import jingswiper from "@/components/jing-swiper/jing-swiper.vue"
export default {
	data() {
		return {
			id:0,
			num:0,
			yu:10.00,
			card:[1,2,3],
			index:0,
		};
	},
	onLoad(options) {   
		this._load()
	},
	components: {jingswiper,uniIcon},
	computed: {  
			    bank_bg: function () { 
					let type=this.index
					if(type==1){
						return 'renmin';
					}
					if(type==2){
						return 'nongye';
					}
					if(type==3){
						return 'gongshang';
					}
					if(type==4){
						return 'jianshe';
					}
					return '';	
			    }
			},
	methods:{
		_load(){
			// this.$api.http.post('shop_cms/get_sale_money').then(res=>{
			// 	if(res.ktx_all<=0){
			// 		res.ktx_all=0
			// 	}
			// 	this.yu=res.ktx_all
			// })
			// this.$api.http.get('shop_cms/get_card').then(res=>{
			// 	this.card=res
			// })	
			this.card=this.$api.json_cms.get_card
			const yue=this.$api.json_cms.get_sale_money
			if(yue.ktx_all<=0){
				yue.ktx_all=0
			}
			
			this.yu=yue.ktx_all
		},
		changeindex(e){
			this.index=e.detail.current
			console.log('abc',e)
		},
		jump_card(index=''){  
			if(index===''){
				uni.navigateTo({
					url:'/pages/cms/user/fenxiao/addcard/addcard?type=add'
				})
			}
			else{
				const card=this.card[index]
				uni.setStorageSync('bank',card)
				uni.navigateTo({ 
					url:'/pages/cms/user/fenxiao/addcard/addcard'
				})
			}
			
		},
		qdtx(){ 
			
			if(this.num>this.yu){
				this.$api.msg('提现金额错误！')
			}
			else{
				uni.navigateTo({
							url:'/pages/cms/user/fenxiao/success/success?'
				 		})
				// const card_id=this.card[this.index].id
				// this.$api.http.post('shop_cms/send_tx',{price:this.num,card_id:card_id}).then(res=>{ 
				// 	if(res*1<1){
				// 		return;
				// 	}
				// 	this.$api.msg('操作成功')
				// 	setTimeout(()=>{
				// 		uni.navigateTo({
				// 			url:'/pages/user/fenxiao/success/success?id='+res
				// 		})
				// 	},1000)
				// 	
				// })	
				
			}	
		},
		bgc(type){ 
			if(type==0){
				return '';
			}
			if(type==1){
				return 'renmin';
			}
			if(type==2){
				return 'nongye';
			}
			if(type==3){
				return 'gongshang';
			}
			if(type==4){
				return 'jianshe';
			}
			
		},
		whole(){
			this.num=this.yu
		}
	},
	onPullDownRefresh() {
		this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
